import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'ucs-opcoes',
  templateUrl: './opcoes.component.html',
  styleUrls: ['./opcoes.component.scss']
})
export class OpcoesComponent implements OnInit, OnDestroy {

  tipo: string;
  private sub: any;

  messagem: string;

  constructor(private route: ActivatedRoute) {}

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
       this.tipo = params['tipo'];
       this.messagem = (this.tipo == "empresa") ? "Oferecemos os melhores seguros para sua empresa" : "Oferecemos os melhores seguros para você e sua família";
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}
